<template>
  <div class="cart__block">
    <ul class="cart__orders">
      <li class="cart__order" v-for="item in products" :key="item.productId" :item="item">
        <h3>{{ item.product.title }}</h3>
        <b>{{ (item.amount * item.product.price) | numberFormat }} ₽</b>
        <span>Артикул: {{ item.product.id }}</span>
      </li>
    </ul>

    <div class="cart__total">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <b>{{ $store.state.cartProducts.length }}</b> {{ numToStr($store.state.cartProducts.length, ['товар', 'товара', 'товаров']) }} на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
    </div>

    <button class="cart__button button button--primery" type="submit">
      Оформить заказ
    </button>
  </div>
</template>

<script>
  import numberFormat from '@/helpers/numberFormat';
  import { mapGetters } from 'vuex';
  export default {
    computed: {
    ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'})
  },
    methods: {
      numToStr(num, arrText) {
      if (num % 10 === 1 && num % 100 !== 11) {
        return arrText[0];
      } else if (num % 10 >= 2 && num % 10 <= 4 && (num % 100 < 10 || num % 100 >= 20)) {
        return arrText[1];
      }
      return arrText[2];
  }
},
  filters: {numberFormat}
}
</script>
