<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ $store.state.cartProducts.length }} {{ numToStr($store.state.cartProducts.length, ['товар', 'товара', 'товаров']) }}
      </span>
    </div>

    <div v-if="formSending">
      <img src="img/preloader.gif" alt="Загрузка" style="margin-left: 45%; margin-bottom: 40px">
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя"/>
            <BaseFormText v-model="formData.address" :error="formError.address" title="Адрес доставки" placeholder="Введите ваш адрес"/>
            <BaseFormText v-model="formData.phone" :error="formError.phone" title="Телефон" placeholder="Введите ваш телефон"/>
            <BaseFormText v-model="formData.email" :error="formError.email" title="Email" placeholder="Введите ваш Email"/>
            <BaseFormTextarea title="Комментарий к заказу" v-model="formData.comment" :error="formError.comment" placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked>
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <OrderProductList/>

        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
           {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import OrderProductList from '@/components/OrderProductList';
  import BaseFormText from '@/components/BaseFormText';
  import BaseFormTextarea from '@/components/BaseFormTextarea';
  import axios from 'axios';
  import { API_BASE_URL } from '@/config';
  export default {
    components: {BaseFormText, BaseFormTextarea, OrderProductList},
    data(){
      return {
        formData: {},
        formError: {},
        formErrorMessage: '',
        formSending: false
      }
    },
    methods: {
      order(){
        this.formError = {};
        this.formErrorMessage = '';
        this.formSending = true;
        clearTimeout(this.formSendingTimer);
        this.formSendingTimer = setTimeout(() => {
        axios
            .post(API_BASE_URL + '/api/orders', {
              ...this.formData
            }, {
               params: {
                userAccessKey: this.$store.state.userAccessKey
              }
            })
            .then(response => {
              this.$store.commit('resetCart');
              this.$store.commit('updateOrderInfo', response.data);
              this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
            })
            .catch(error => {
              this.formError = error.response.data.error.request || {};
              this.formErrorMessage = error.response.data.error.message;
            })
            .then(() => this.formSending = false);
          }, 1500)
        },

      numToStr(num, arrText) {
      if (num % 10 === 1 && num % 100 !== 11) {
        return arrText[0];
      } else if (num % 10 >= 2 && num % 10 <= 4 && (num % 100 < 10 || num % 100 >= 20)) {
        return arrText[1];
      }
      return arrText[2];
  }
}
}

</script>
