<template>
  <main class="content container" v-if="$store.state.cartLoading">
    <div>
      <img src="img/preloader.gif" alt="Загрузка" style="margin-left: 45%">
    </div>
  </main>
  <main class="content container" v-else-if="$store.state.cartLoadingFailed">
    Не удалось загрузить корзину
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
      {{ $store.state.cartProducts.length }} {{ numToStr($store.state.cartProducts.length, ['товар', 'товара', 'товаров']) }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" v-show="products.length > 0"  :to="{name: 'order'}" class="cart__button button button--primery" type="submit">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import CartItem from '@/components/CartItem';

export default {
  filters: {numberFormat},
  components: {CartItem},

  computed: {
    ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'})
  },

  methods: {
  numToStr(num, arrText) {
  if (num % 10 === 1 && num % 100 !== 11) {
    return arrText[0];
  } else if (num % 10 >= 2 && num % 10 <= 4 && (num % 100 < 10 || num % 100 >= 20)) {
    return arrText[1];
  }
  return arrText[2];
}
}
}
</script>
